import Vue from 'vue'
import ElementUI from '../static/my_home/element.js';
import whole from './components/my_home/whole.vue'
import "babel-polyfill";
Vue.config.productionTip = false
Vue.use(ElementUI);

new Vue({
	el: '#app',
	template: '<whole />',
	components: {
		whole: whole
	}
})



