<template>
	<div>
		<router-view/>
	</div>
</template>

<script>
	import Vue from 'vue'
	import VueRouter from 'vue-router'
	import home from './home'
	Vue.use(VueRouter)
	var router = new VueRouter({
		routes: [
		{
			path: '/all',
			name: 'home',
			component: home
		}
		]
	})
	
	export default{
		router: router,
	}
</script>

<style>
</style>


