<template>
	<div class="home clearfix">
		<el-card class="head-contain" >
			<div class="header clearfix">
				<a class="header_info sp">
					光影
				</a>
				<a class="header_info sp">
					首页
				</a>
				<div class="search">
					<input placeholder="输入电影"><el-button type="primary" size="small">搜索</el-button>
				</div>
				<div class="people" @mouseover="info_show=true" @mouseleave="info_show=false">
					<img src="static/movie/info.jpg"/>
					<div class="person_info clearfix" v-show="info_show">
					<div class="keke" @click="my_home">
						我的主页
					</div>
					<el-divider></el-divider>
					<div class="keke" @click="aa" style="cursor: pointer;">
						退出账号
					</div>
				</div>
				</div>
			</div>
		</el-card>
		<div class="body_contain">
			<div class="body_title">
				<div class="body_wrap">
					<div class="body_up">
						<img src="../../../static/my_home/my-bg.jpg"/>
					</div>
					<div class="body_down clearfix">
						<div class="people clearfix">
							<img src="../../../static/my_home/info.jpg"/>
						</div>
						<div class="text">
							<div class="tit1">OK</div>
							<div class="tit2">
								个人简介<br />
								喜欢唱，跳，rap
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="body_content">
				<div class="body_left">
					<div class="info">
						<div class="info_title">
							<div class="info_lists" v-for="(item,index) in title" @click="show(index)">
								{{item.name}}{{item.num}}
							</div>
						</div>
						<div class="info_content">
							<div class="info_block" v-show="list_num[0]==2">
								<div class="title">
									我的动态
								</div>
								<div class="block" v-for="item in info">
									<div class="block_title">
										<span class="sp_left" v-if="item.sty=='yp'">
											写了影评
										</span>
										<span class="sp_left" v-if="item.sty=='ly'">
											写了留言
										</span>
										<span class="sp_right">
											{{item.time}}
										</span>
									</div>
									<div class="block_content clearfix">
										<div class="block_content_left">
											<a :href="item.hre" style="display: block;height: 100%;" @click="href_href(index)">
												<img :src="item.id"/>
											</a>
											
										</div>
										<div class="block_content_right">
											{{item.text}}
										</div>
									</div>
								</div>
							</div>
							<div class="info_block" v-show="list_num[1]==2">
								<div class="title">
									我的影评
								</div>
								<div class="block" v-for="item in yp">
									<div class="block_title">
										<span class="sp_left">
											写了影评
										</span>
										<span class="sp_right">
											{{item.time}}
										</span>
									</div>
									<div class="block_content clearfix">
										<div class="block_content_left">
											<a :href="item.hre" style="display: block;height: 100%;" @click="href_href(index)">
												<img :src="item.id"/>
											</a>
											
										</div>
										<div class="block_content_right">
											{{item.text}}
										</div>
									</div>
								</div>
							</div>
							<div class="info_block" v-show="list_num[2]==2">
								<div class="title">
									我的留言
								</div>
								<div class="block" v-for="item in ly">
									<div class="block_title">
										<span class="sp_left">
											写了留言
										</span>
										<span class="sp_right">
											{{item.time}}
										</span>
									</div>
									<div class="block_content clearfix">
										<div class="block_content_left">
											<a :href="item.hre" style="display: block;height: 100%;" @click="href_href(index)">
												<img :src="item.id"/>
											</a>
										</div>
										<div class="block_content_right">
											{{item.text}}
										</div>
									</div>
								</div>
							</div>
							<div class="info_block" v-show="list_num[3]==2">
								<div class="title">
									我的回复
								</div>
								<div class="none">
									暂无数据
								</div>
							</div>
							<div class="info_block" v-show="list_num[4]==2">
								<div class="title">
									我的消息
								</div>
								<div class="none">
									暂无数据
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="body_right">
					其他
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data:function(){
			return {
				info_show:false,
				title:[{name:'动态',num:3},{name:'影评',num:1},{name:'留言',num:2},{name:'回复',num:0},{name:'消息',num:0}],
				list_num:[2,1,1,1,1],
				info:
				[
				{sty:'yp',time:'2019-06-23 22:59',id:'https://img3.doubanio.com/view/photo/s_ratio_poster/public/p2549558913.jpg',text:'hehehehe',hre:'a'},
				{sty:'ly',time:'2019-06-15 07:59',id:'https://img1.doubanio.com/view/photo/s_ratio_poster/public/p2555859678.jpg',text:'heheheh',hre:'a'},
				{sty:'ly',time:'2019-06-14 13:25',id:'https://img3.doubanio.com/view/photo/s_ratio_poster/public/p2554525534.jpg',text:'hehehe',hre:'a'},
				]
			}
		},
		computed:{
			yp:function(){
				return this.info.filter(function(item){
					return item.sty=='yp';
				})
			},
			ly:function(){
				return this.info.filter(function(item){
					return item.sty=='ly';
				})
			}
		},
		methods:{
			href_href:function(ac){
				alert(4)
			},
			show:function(ac){
				for(var i in this.list_num){
					this.$set(this.list_num,i,1)
				}
				this.$set(this.list_num,ac,2)
			},
			my_home:function(){
				console.log(3)
			},
			aa:function(){
				this.$router.push({
						path: '/login',
					})
				}
		},
		beforeCreate:function(){
			
		},
		created:function(){
			document.title='我的动态'
		}
	}
</script>

<style lang="scss">
	body{
		font-family: Chinese Quote,-apple-system,BlinkMacSystemFont,Segoe UI,PingFang SC,Hiragino Sans GB,Microsoft YaHei,Helvetica Neue,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
		font-size: 15px;
		color:#303133;
		a{
			color:#1890ff;
			text-decoration: none;
			font-size: 14px;
		}
	}
	.el-divider--horizontal{
		margin: 0;
	}
	.home .sp{
		color:#1890ff;
	}
	.body_contain{
		width: 1000px;
		position: relative;
		margin: 70px auto 0 auto;
		
		.body_content{
			margin-top: 20px;
			position: relative;
			.body_right{
				position: absolute;
				top: 0;
				right: 0;
				width: 300px;
				height: 100px;
				box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
				text-align: center;
			}
			.body_left{
				position: relative;
				margin-right: 320px;
				box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
				.info{
					.info_title{
						height: 40px;
						line-height: 40px;
						border: 1px solid #e4e7ed;
						.info_lists{
							font-size: 14px;
							text-align: center;
							float: left;
							width: 44px;
							padding: 0 20px;
							border-right: 1px solid #e4e7ed;
							cursor: pointer;
						}
					}
					.info_content{
						.info_block{
							margin: 20px;
							.title{
								height: 50px;
								line-height: 50px;
								font-weight: bolder;
								font-size: 16px;
								border-bottom: 1px solid #e4e7ed;
							}
							.block{
								padding: 20px;
								.block_title{
									color: #8590a6;
									height: 34px;
									line-height: 34px;
									.sp_right{
										float: right;
									}
								}
								.block_content{
									.block_content_left{
									height: 142px;
									width: 90px;
									float: left;
									img{
										height: 100%;
										width: 100%;
									}
									}
									.block_content_right{
										float: left;
										margin-left: 20px;
									}
								}
							}
							.none{
								padding: 20px;
								height: 176px;
								text-align: center;
							}
						}
					}
				}
			}
		}
		
		.body_title{
			box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
			.body_wrap{
				.body_up{
					height: 132px;
					width: 100%;
					overflow: hidden;
					img{
						width: 100%;
					}
				}
				.body_down{
					position: relative;
					.people{
						float: left;
						z-index: 5;
						border: 4px solid white;
						position: absolute;
						top: -50px;
						left: 24px;
						border-radius:8px;
						img{
							height: 160px;
							width: 160px;
						}
					}
					.text{
						height: 145px;
						position: relative;
						left: 230px;
						.tit1{
							font-size: 22px;
							font-weight: bolder;
							height: 60px;
							line-height: 60px;
						}
					}
				}
			}
		}
		
	}
	
	
	
	
	
	
	
	
	
	
	
	
	
	
	
	
	
	
	
	.head-contain{
		background: white;
		.el-card__body{
			padding: 0;
		}
		width: 100%;
		height: 52px;
		position: fixed;
		z-index: 4;
		.header{
			position: relative;
			width: 1000px;
			margin:0 auto;
			.header_info{
				margin: 0 20px;
				line-height: 52px;
			}
			.search{
				input{
					padding-left: 20px;
					border: 1px solid #dcdfe6;
					height: 28px;
					width: 300px;
					border-radius:  5px 0 0 5px;
				}
				button{
					border-radius:  0 5px 5px 0;
				}
				display: inline-block;
				position: absolute;
				left: 30%;
				height: 52px;
				line-height: 52px;
			}
			.person_info{
				position: absolute;
				right: 0;
				top: 52px;
				height: 100px;
				width: 100px;
				background: white;
				.keke{
					height: 30px;
					line-height: 30px;
					padding: 10px 20px;
					cursor: pointer;
				}
			}
			.people{
				float: right;
				height: 52px;
				width: 52px;
				border-radius:50%;
				overflow: hidden;
				img{
					height: 100%;
					width: 100%;
				}
			}
		}
	}
</style>


